import { useState } from "react";
import styles from "../App.module.css";
import ScrollTop from "./ScrollToTop";
import ShareApp from "./ShareApp";
import LoadingOpening from "./Loading";

function Opinion() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [text, setText] = useState("");
  const [number, setNumber] = useState("");

  function handleSubmission() {
    localStorage.clear();
    let userData = {
      Name: name,
      Email: email,
      Text: text,
      Number: number,
    };
    localStorage.setItem("userInfo", JSON.stringify(userData));
    document.getElementsByClassName("span").style = "block";
    alert("Data recorded!");
    window.location.reload();
  }

  return (
    <div>
      <LoadingOpening />
      <h1></h1>
      <div>
        <div className={styles["contactUsWrapper"]}>
          <h5></h5>
          <form>
            <label htmlFor="name">
              <input
                id="enter-name"
                type="text"
                className={styles["txtfield w-input"]}
                name="enter-name"
                data-name="enter-name"
                placeholder=""
                onChange={(e) => setName(e.target.value)}
                style={{}}
              />
            </label>

            <label htmlFor="tel">
              <input
                id="enter-tel"
                type="text"
                className={styles["txtfield w-input"]}
                name="enter-tel2"
                placeholder=""
                onChange={(e) => setNumber(e.target.value)}
                style={{}}
              />
            </label>

            <label htmlFor="email">
              <input
                id="enter-email"
                type="text"
                className={styles["txtfield w-input"]}
                name="enter-email2"
                placeholder=""
                onChange={(e) => setEmail(e.target.value)}
                style={{}}
              />
            </label>

            <label htmlFor="text">
              <textarea
                name="enter-content"
                id="enter-content"
                title=""
                placeholder=""
                className={`txt_area w-input ${styles.textarea}`}
                onChange={(e) => setText(e.target.value)}
                style={{}}
              />
            </label>
          </form>

          <br />
        </div>

        <div className={styles["subscribeButtonWrapper"]}>
          <button onClick={handleSubmission} className="subscribeButton">
            
          </button>
        </div>
      </div>
      <div
        className={`${styles.shareFooterWrapper} d-flex justify-center align-center`}
      >
        <div className="centered-container">
          <ShareApp />
        </div>
        <ScrollTop />
      </div>
      <div className={styles["footer-wrapper"]}>
        <footer> </footer>
        <footer> 202418 1200</footer>
      </div>
    </div>
  );
}

export default Opinion;
